<template>
  <div class="sender">
    <h2>Izaberite Vase slike</h2> 
    <div class="file-inputs-container">
      <input type="file" accept="image/*" id="file-upload" class="file-input" @change="moreFields">
      <input type="file" accept="image/*" id="file-upload" class="file-input" 
      v-for="number,index in uploads" 
      :key="index" 
      @change="moreFields">
    </div>
    <!--<button v-on:click="uploadFile">click me</button>-->
  </div>
</template>

<script>
import 'dropbox';
import UploadImage from 'vue-upload-image';
var Dropbox = require("dropbox");
var dropbox = new Dropbox.Dropbox({ accessToken: 'WefUJKb-W5AAAAAAAAAAY6uPQaxEOKHjgPHjYF4z1swFvzwulSEAdI7x8PESOOiD', fetch: fetch });
export default {
  name: 'sender',
  components: {
    UploadImage
  },
  data () {
    return {
      uploads: 0,
      structure: []
    }
  },
  methods: {
  	getFields(element) {
  		if(element.value == ''){
       element.parentElement.removeChild(element);
       return;
     }

     var number = document.getElementById("container").childElementCount;
     var container = document.getElementById("container");
     var input = document.createElement("input");

     input.type = "file";
     input.name = "fileToUpload"+((number-6)/2+1);
     input.id = "fileToUpload"+((number-6)/2+1);
     input.setAttribute("onchange", 'addFields(this)');
     container.insertBefore(input, container.children[number-1]);

     container.insertBefore(document.createElement("br"), container.children[number]);
   },
   dropbox() {
    return new Dropbox.Dropbox({ accessToken: 'WefUJKb-W5AAAAAAAAAAY6uPQaxEOKHjgPHjYF4z1swFvzwulSEAdI7x8PESOOiD', fetch: fetch });
  },
  uploadFile(name) {
    var fileInput = document.querySelectorAll('.file-input');
    this.dropbox().filesCreateFolder({path: '/' + name })
    .then(function() {
    })
    .catch(function(error) {
    });
    fileInput.forEach(function(item) {
      var file = item.files[0];
      if(file !== undefined) {
        var Dropbox = require("dropbox");
        var innerdropbox =  new Dropbox.Dropbox({ accessToken: 'WefUJKb-W5AAAAAAAAAAY6uPQaxEOKHjgPHjYF4z1swFvzwulSEAdI7x8PESOOiD', fetch: fetch });
        innerdropbox.filesUpload({path: '/' + name + '/' + file.name, contents: file })
        .then(function(response) {
          var button = document.getElementById('submit-button');
          button.innerHTML = "Uspesno prosledjeno";
        })
        .catch(function(error) {
          var button = document.getElementById('submit-button');
          button.innerHTML = "Greska,pokusajte opet";
        });
      }
    })   
  },
  moreFields () {
    let inputs = document.querySelectorAll(".file-input");
    if(inputs[0].value) {
      this.uploads ++
    }
    else {
      console.log("something went wrong", inputs[0].value)
    }
  }
},
computed: {
  getDropbox () {
    this.dropbox().filesListFolder({path:'/online foto'})
    .then(response => {
      console.log('this is response from folders',response.entries);
      this.structure = response.entries;
    })
    .catch(error => {
      console.log(error)
    })
    this.dropbox().usersGetCurrentAccount()
    .then(function(response) {
    })
    .catch(function(error) {
    });
  }
},
watch: {
  uploads: function(val) {
    this.$emit('number', val)
  }
}
}
</script>

<style lang="scss" scoped>
@import '@design';
.sender {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justfy-content: center;
  opacity: 1;
  padding-bottom: 50px;

  @include media-breakpoint-up(md) {
    padding-bottom: 0;
  }

  h2 {
    padding: 1rem;
    background: rgba(255,255,255,0.6);
    border-radius: 25px;
    font-size: 20px;

    @include media-breakpoint-up(md) {
      font-size: 2rem;
    }
  }

  .upload-image {
    height: 20vh;
    width: 30vw;
  }
}

.file-inputs-container {
  width: 100%;
  height: 100%;
  margin-top: 2rem;

  .file-input {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    cursor: pointer;
    height: auto;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    background: rgba(255,255,255,0.6);
    position: relative;

    &:after {
      content: 'Izaberite sliku';
      height: 100%;
      width: 110px;
      background: black;
      color: white;
      position: absolute;
      top: 0;
      left: 0;
      pointer-events: none;
      font-size: 12px;
      display: flex;
      align-items: center;
      justify-content: center;

    }

    button {
      cursor: pointer;
    }

    label {
      background: red;
      padding: 10px;
    }
  }
}


</style>
