<template>
  <section class="home-page">
    <div class="container-fluid">
      <div class="row no-gutters">
        <div class="home-page__title col-sm-16 col-lg-7">
          <div class="header__wrapper">
            <div class="home-page__header__container">
              <h3>Dobrodosli,<br>ovde mozete poruciti Vase najomiljenije fotografije koje Vam stizu na kucnu adresu u najbrzem mogucem roku.</h3>
              <h4>Uputstva:</h4>
              <p><strong>1.</strong> U donjem delu sajta kliknite na dugme "Izaberite sliku", tad ce Vam otvoriti pretrazivac za Vas racunar i preko njega pronadjite zeljenu sliku. Kada izaberete zeljenju sliku,otvorice Vam se opcija da izaberete sledecu. Maksimalan broj razlicitih slika je 30 komada.</p>
              <p><strong>2.</strong> Nakon izabranih slika izaberite format,papir i broj kopija u kojem zelite slike. Napomena,ako navodite broj kopija za neku odredjenu sliku,molimo Vas da to unesete u napomenu. U napomeni navedite koja je slika u pitanju (ime slike) i kolicinu.</p>
              <p><strong>3.</strong> Popunite Vase lice podatke za porudzbinu</p>
              <p><strong>4.</strong> Nakon popunjenih svih podataka prikazacemo Vam cenu Vase narudzbine (bez cene dostave). Klikom na dugme "Prosledi porudzbinu" cete uspesno proslediti Vasu porudzbinu. Kada porudzbina stigne u fotokopirnicu, dobicete SMS poruku.</p>
              <p class="mobile-app-headsup">U dnu naseg sajta mozete preuzeti i mobilne aplikacije za IOS i Android ako zelite da nam saljete Vase slike sa Vaseg telefona.</p>
