<script>
import Sender from './Sender.vue'
  export default {
    name: 'home-page',
    data () {
      return {
        paper: undefined,
        copies: 1,
        size: undefined,
        photos: undefined,
        price: undefined
      }
    },
    components: {
      Sender
    },
    computed: {
      getData () {
        paper: this.paper;
        copies: this.copies;
        size: this.size;
        photos: this.photos;
        price: this.price;
      },
      getPaperPrice () {
        let paperprice = this.$refs.paper.value;
        if(paperprice && paperprice === 'obican') {
          this.paper = 15
        }
        if(paperprice && paperprice === 'masni') {
          this.paper = 18
        }
        if(paperprice && paperprice === 'pojacani') {
          this.paper = 24
        }
      },
      getCopiesPrice () {
        let copiesprice = this.$refs.copies.value;
        if(copiesprice) {
          this.copies = copiesprice
        }
      },
      getSizesPrice () {
        let sizesprice = this.$refs.size.value;
        if(sizeprice && sizeprice === '13x10') {
          this.size = 135
        }
        if(sizeprice && sizeprice === '15x12') {
          this.size = 170
        }
        if(sizeprice && sizeprice === '18x18') {
          this.size = 200
        }
      }
    },
    methods: {
      submitPhotos () {
        this.$validator.validate().then(valid => {
          if (valid) {
            this.$children[0].uploadFile(this.$refs.person.value)
            this.$refs.detailsform.submit()

          }
        }); 
      },
      numberOfPhotos (value) {
        this.photos = value
      }
    },
    watch: {
      getData: function() {
        console.log(this.paper)
        console.log(this.size)
        console.log(this.copies)
        console.log(this.photos)
        if(this.paper && this.size && this.copies && this.photos) {
          let price = ((this.paper + this.size) * this.photos) * this.copies
          console.log(price)

        }
      }
    }
  }
</script>

